import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { injectIntl } from 'react-intl';
import Button from 'ui-library/lib/components/buttons/Button';
import Modal from 'ui-library/lib/components/general/ModalButton';

import ModelSummary from '../../summaries/ModelSummary/ModelSummary';
import intlShape from '../../../../utils/intlPropType';

export class ViewSummaryButton extends Component {
  state = {
    isModalOpen: false,
  }

  handleCloseModal = () => {
    this.setState({ isModalOpen: false });
  }

  handleOpenModal = () => {
    this.setState({ isModalOpen: true });
  }

  render() {
    const {
      intl,
      modalInfo,
    } = this.props;

    return (
      <>
        <Button
          className="view-profile-button"
          inline
          label={intl.formatMessage({ id: 'components.column-list-item.view-profile' })}
          onClick={this.handleOpenModal}
        />

        <Modal
          modalTitle={
            intl.formatMessage(
              { id: 'components.column-list-item.modal-title' },
              { type: modalInfo.displayName },
            )
          }
          closeOnBgClick
          expanded={this.state.isModalOpen}
          onOpen={this.handleOpenModal}
          onClose={this.handleCloseModal}
        >
          <ModelSummary
            id={modalInfo.item.id}
            attributes={modalInfo.item.attributes}
            meta={modalInfo.attributeMeta}
            showCategories={false}
          />
        </Modal>
      </>
    );
  }
}

ViewSummaryButton.propTypes = {
  intl: intlShape.isRequired,
  modalInfo: PropTypes.shape({
    attributeMeta: PropTypes.shape({}).isRequired,
    displayName: PropTypes.string.isRequired,
    item: PropTypes.shape({
      id: PropTypes.string.isRequired,
      attributes: PropTypes.shape({}).isRequired,
    }).isRequired,
  }).isRequired,
};

export default injectIntl(ViewSummaryButton);
