import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { FormattedMessage, injectIntl } from 'react-intl';
import { generatePath } from 'react-router';
import Spinner from 'ui-library/lib/components/general/Spinner';
import LeftNavBarActions from 'ui-library/lib/components/panels/left-nav/Actions';
import InlineMessage, { MessageTypes } from 'ui-library/lib/components/general/InlineMessage';
import InputRow from 'ui-library/lib/components/layout/InputRow';

import {
  filterResourceTypesAction,
  setSelectedResourceTypeAction,
} from '../../../../store/actions/resourceTypes/resourceTypes';
import ModelFormV2 from '../../forms/ModelFormV2/ModelFormV2';
import { PROP_MODEL_TYPES } from '../../../../utils/modelTypes';
import * as routes from '../../../../utils/routes';
import ResourceTypeContainer from '../../../../containers/ResourceTypeContainer/ResourceTypeContainer';
import { RESOURCE_TYPES, PROP_RESOURCE_TYPES, getResourceById } from '../../../../utils/resourceTypes';
import { PERMISSIONS } from '../../../../utils/permissions/permissions';
import Page from '../../layout/Page/Page';
import { getTitleOrPrimaryAttributeValue } from '../../../../utils/helpers';
import DropDownList from '../../list-inputs/DropDownList/DropDownList';

export class NewModelPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      areFieldsValid: true,
      createdModel: undefined,
      fields: {
        genericDropdown: {
          errorMessage: 'validation.required',
          label: 'containers.model-form.dropdown-label',
          labelPrompt: 'components.search-bar-drop-down.list-prompt',
          options: undefined,
          required: true,
          selectedOption: undefined,
          showError: false,
          type: RESOURCE_TYPES.GENERIC,
        },
        groupDropdown: {
          errorMessage: 'validation.required',
          label: 'containers.model-form.group-dropdown-label',
          labelPrompt: 'components.search-bar-drop-down.list-prompt',
          options: undefined,
          required: true,
          selectedOption: undefined,
          showError: false,
          type: RESOURCE_TYPES.GROUP,
        },
      },
      isFinishedCreating: false,
      isLoading: true,
    };
  }

  componentDidMount() {
    this.filterResourceTypes();
  }

  componentDidUpdate(prevProps, prevState) {
    /*
     * NOTE: We are ok to use setState here because of the conditional
     * https://reactjs.org/docs/react-component.html#componentdidupdate
     */
    /* eslint-disable react/no-did-update-set-state */

    const {
      match,
      selectedGroupsResource,
      selectedParentResource,
    } = this.props;

    if (prevProps.selectedParentResource !== selectedParentResource) {
      this.setDropDownOptions(selectedParentResource, RESOURCE_TYPES.GENERIC);
    }

    if (prevProps.selectedGroupsResource !== selectedGroupsResource) {
      this.setDropDownOptions(selectedGroupsResource, RESOURCE_TYPES.GROUP);
    }

    if (prevProps.match.params.type !== match.params.type) {
      this.filterResourceTypes();
    }

    if (prevState.fields !== this.state.fields) {
      const { fields } = this.state;
      const optionValues = _.mapValues(fields, 'options');
      const areAllOptionsUndefined = _.every(optionValues, _.isUndefined);
      const areAnySelectedOptionsUndefined = (
        Object
          .values(fields)
          .some(val => _.get(val, 'options') && _.isUndefined(_.get(val, 'selectedOption')))
      );
      if (areAllOptionsUndefined) {
        this.setState({
          areFieldsValid: true,
        });
      } else {
        this.setState({
          areFieldsValid: !areAnySelectedOptionsUndefined,
        });
      }
    }
  }

  setDropDownOptions = (props, type) => {
    const { data, meta } = props;

    if (!data || data.length === 0 || !meta) {
      this.setState(state => ({
        fields: {
          ...state.fields,
          [`${type}Dropdown`]: {
            ...state.fields[`${type}Dropdown`],
            options: undefined,
            selectedOption: undefined,
          },
        },
      }));
      return;
    }

    const options = data.map((item, index) => ({
      id: item.id,
      label: getTitleOrPrimaryAttributeValue(item.attributes, meta),
      type: item.type,
      value: toString(index),
    }));

    this.setState((state) => {
      const selectedOption =
        (options && options.length === 1) ?
          options[0] :
          state.fields[`${type}Dropdown`].selectedOption;
      return ({
        fields: {
          ...state.fields,
          [`${type}Dropdown`]: {
            ...state.fields[`${type}Dropdown`],
            options,
            selectedOption,
          },
        },
      });
    });
  }

  getDisplayName = (type) => {
    const {
      selectedParentResourceTypeId,
    } = this.props;
    const types = {
      generic: getResourceById(selectedParentResourceTypeId),
    };
    return _.get(types[type], 'attributes.displayName');
  }

  async filterResourceTypes() {
    const { resourceType, match } = this.props;
    const filter = {
      byType: [resourceType],
      byPermission: PERMISSIONS.CREATE,
    };

    if (resourceType === RESOURCE_TYPES.GENERIC) {
      const resourceId = _.get(match, 'params.type');
      const selected = getResourceById(resourceId);
      await this.props.setSelectedResourceTypeAction(selected, true);
    } else {
      await this.props.filterResourceTypesAction(filter, {
        getParent: true,
        getGroups: true,
      });
    }

    this.setState({ isLoading: false });
  }

  handleModelCreate = async (attrs) => {
    const { fields } = this.state;
    let relationshipOptions;
    Object.keys(fields).forEach((key) => {
      const { selectedOption } = fields[key];
      if (selectedOption) {
        relationshipOptions = {
          ...relationshipOptions,
          [key]: selectedOption,
        };
      }
    });
    try {
      const res = await this.props.createAction(attrs, relationshipOptions);
      this.setState({ isFinishedCreating: true, createdModel: res.data });
    } catch (e) {
      // Handle errors in actions
    }
  }

  handleDropDownChange = (selectedOption, type) => {
    this.setState(state => ({
      fields: {
        ...state.fields,
        [`${type}Dropdown`]: {
          ...state.fields[`${type}Dropdown`],
          selectedOption,
        },
      },
    }));
  };

  handleDropDownToggle = (type) => {
    this.setState((state) => {
      const {
        required,
        selectedOption,
      } = state.fields[`${type}Dropdown`];
      return ({
        fields: {
          ...state.fields,
          [`${type}Dropdown`]: {
            ...state.fields[`${type}Dropdown`],
            showError: required && !selectedOption,
          },
        },
      });
    });
  }

  handleDropDownReset = () => {
    const { fields } = this.state;
    Object.keys(fields).forEach((key) => {
      const { options } = fields[key];
      if (options && options.length > 1) {
        this.setState(state => ({
          fields: {
            ...state.fields,
            [key]: {
              ...state.fields[key],
              selectedOption: undefined,
              showError: false,
            },
          },
        }));
      }
    });
  }

  render() {
    const {
      meta,
      modelType,
      resourceType,
      selected,
      selectedGroupsResource,
      selectedParentResource,
      selectedParentResourceTypeId,
      title,
    } = this.props;
    const {
      areFieldsValid,
      createdModel,
      fields,
      isFinishedCreating,
      isLoading,
    } = this.state;
    const createdModelId = _.get(createdModel, 'data.id', undefined);
    const createdModelType = _.get(createdModel, 'data.type', undefined);
    const parentError = selectedParentResource.error;
    const showSpinner = !!(
      isLoading || selectedParentResource.isRequesting || selectedGroupsResource.isRequesting
    );
    if (isFinishedCreating && createdModelId && createdModelType) {
      return (
        <Redirect
          to={{
            pathname: generatePath(routes.PROFILE_ROUTE, {
              model: modelType,
              type: createdModelType,
              id: createdModelId,
            }),
          }}
        />
      );
    }
    return (
      <Page
        className="NewModelPage"
        title={title}
      >
        <Spinner show={showSpinner}>
          <div className="page-body">
            {
              resourceType !== RESOURCE_TYPES.GENERIC &&
              <ResourceTypeContainer
                filter={{
                  byPermission: PERMISSIONS.CREATE,
                  byType: [resourceType],
                }}
                getParent
                getGroups
                showLabel
              />
            }
            {
              Object.keys(fields).map((key) => {
                const {
                  errorMessage,
                  label,
                  labelPrompt,
                  options,
                  required,
                  selectedOption,
                  showError,
                  type,
                 } = fields[key];

                 if (options && options.length > 1) {
                  return (
                    <InputRow key={key}>
                      <DropDownList
                        errorMessage={(showError) ? errorMessage : ''}
                        handleOnToggle={() => this.handleDropDownToggle(type)}
                        label={{
                          id: label,
                          displayName: this.getDisplayName(type),
                        }}
                        labelPrompt={labelPrompt}
                        handleOnChange={option => this.handleDropDownChange(option, type)}
                        options={options}
                        required={required}
                        selectedOption={selectedOption}
                      />
                    </InputRow>
                   );
                 }
                 return null;
              })
            }
            {
              parentError
              ? (
                <InlineMessage type={MessageTypes.ERROR}>
                  <FormattedMessage
                    id="actions.resource-types.get-parent-failure"
                    values={{
                      parentResource: selectedParentResourceTypeId,
                    }}
                  />
                </InlineMessage>
              )
              : (
                <ModelFormV2
                  areOutsideFormFieldsValid={areFieldsValid}
                  meta={meta}
                  onReset={this.handleDropDownReset}
                  onSave={this.handleModelCreate}
                  selected={selected}
                />
              )
            }
          </div>
        </Spinner>
      </Page>
    );
  }
}

NewModelPage.propTypes = {
  createAction: PropTypes.func.isRequired,
  filterResourceTypesAction: PropTypes.func.isRequired,
  match: PropTypes.shape({
    params: PropTypes.shape({
      type: PropTypes.string,
    }).isRequired,
  }),
  meta: PropTypes.shape({
    attributeCategories: PropTypes.arrayOf(PropTypes.string),
    attributes: PropTypes.shape({}).isRequired,
  }).isRequired,
  modelType: PropTypes.oneOf(PROP_MODEL_TYPES).isRequired,
  resourceType: PropTypes.oneOf(PROP_RESOURCE_TYPES).isRequired,
  selected: PropTypes.shape({
    id: PropTypes.string.isRequired,
    attributes: PropTypes.shape({
      parentResourceType: PropTypes.string,
    }).isRequired,
  }).isRequired,
  selectedGroupsResource: PropTypes.shape({
    data: PropTypes.arrayOf(PropTypes.shape({})),
    meta: PropTypes.shape({}),
    error: PropTypes.string,
    isRequesting: PropTypes.bool,
  }),
  selectedParentResource: PropTypes.shape({
    data: PropTypes.arrayOf(PropTypes.shape({})),
    meta: PropTypes.shape({}),
    error: PropTypes.string,
    isRequesting: PropTypes.bool,
  }),
  selectedParentResourceTypeId: PropTypes.string,
  setSelectedResourceTypeAction: PropTypes.func.isRequired,
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
};

NewModelPage.defaultProps = {
  match: {
    params: {
      type: undefined,
    },
  },
  selectedGroupsResource: {
    data: [],
    meta: undefined,
    error: undefined,
    isRequesting: false,
  },
  selectedParentResource: {
    data: [],
    meta: undefined,
    error: undefined,
    isRequesting: false,
  },
  selectedParentResourceTypeId: undefined,
  title: '',
};

function mapStateToProps(state) {
  const {
    selected,
    selectedParentResource,
    selectedGroupsResource,
  } = state.resourceTypes;
  const selectedParentResourceTypeId = selected.attributes.parentResourceType;
  const meta = selected.attributes;

  return {
    meta,
    selected,
    selectedGroupsResource,
    selectedParentResource,
    selectedParentResourceTypeId,
  };
}

export default injectIntl(connect(
  mapStateToProps,
  {
    filterResourceTypesAction,
    selectItemLeftNavAction: LeftNavBarActions.selectItem,
    setSelectedResourceTypeAction,
  },
)(NewModelPage));
