import React from 'react';
import { FormattedMessage } from 'react-intl';
import _ from 'lodash';
import axios from '../../../utils/axiosInstance';
import endpoints from '../../../utils/apiEndpoints';
import { generateHttpErrorMessage, logErrorToConsole, dispatchErrorMessage } from '../../../utils/helpers';
import * as types from '../actionTypes';
import { createAction, REQUEST, SUCCESS, FAILURE } from '../index';
import ParentResourceError from '../../../errors/ParentResourceError';

export const resourceTypesActions = {
  request: () => createAction(types.RESOURCE_TYPES[REQUEST]),
  success: data => createAction(types.RESOURCE_TYPES[SUCCESS], { data }),
  failure: error => createAction(types.RESOURCE_TYPES[FAILURE], { error }),
  selected: selected => createAction(types.SELECT_RESOURCE_TYPE, { selected }),
  selectedSecondary: selected => createAction(types.SELECT_SECONDARY_RESOURCE_TYPE, { selected }),
  parentRequest: () => createAction(types.PARENT_RESOURCES[REQUEST]),
  parentSuccess: data => createAction(types.PARENT_RESOURCES[SUCCESS], { data }),
  parentFailure: error => createAction(types.PARENT_RESOURCES[FAILURE], { error }),
  filter: filter => createAction(types.RESOURCE_TYPES_FILTER, { filter }),
  groupsRequest: () => createAction(types.GROUPS_RESOURCES[REQUEST]),
  groupsSuccess: data => createAction(types.GROUPS_RESOURCES[SUCCESS], { data }),
  groupsFailure: error => createAction(types.GROUPS_RESOURCES[FAILURE], { error }),
  reportFailure: error => createAction(types.RESOURCE_TYPE_REPORT[FAILURE], { error }),
};

export function getResourceTypesAction() {
  return async (dispatch) => {
    try {
      dispatch(resourceTypesActions.request());
      const response = await axios.get(endpoints.resourceTypes());
      dispatch(resourceTypesActions.success(response.data));

      return Promise.resolve(response.data);
    } catch (error) {
      logErrorToConsole(error);
      const { custom } = error;
      let intlKey;

      if (custom) {
        ({ intlKey } = error);
      } else {
        const errorMessage = generateHttpErrorMessage(error);
        ({ intlKey } = errorMessage);
      }

      dispatch(resourceTypesActions.failure(intlKey));

      return Promise.reject(error);
    }
  };
}

export function setSelectedResourceTypeAction(resource, getParent, getGroups) {
  return async (dispatch) => {
    try {
      const hasParent = _.get(resource, 'attributes.parentResourceType');
      dispatch(resourceTypesActions.selected(resource));
      try {
        if (getParent && hasParent) await dispatch(getSelectedParentResourceAction());
        if (getGroups) await dispatch(getSelectedGroupsResourceAction());
      } catch (error) {
        // Need to catch here, but error is handled below
      }
      return Promise.resolve(resource);
    } catch (error) {
      logErrorToConsole(error);
      return Promise.reject(error);
    }
  };
}

export function setSecondarySelectedResourceTypeAction(resource) {
  return async (dispatch) => {
    try {
      dispatch(resourceTypesActions.selectedSecondary(resource));
      return Promise.resolve(resource);
    } catch (error) {
      logErrorToConsole(error);
      return Promise.reject(error);
    }
  };
}

export function getSelectedParentResourceAction() {
  return async (dispatch, getState) => {
    const parentResourceType = _.get(getState(), 'resourceTypes.selected.attributes.parentResourceType');

    try {
      const endpoint = endpoints.endpointForResource(parentResourceType);
      if (_.isUndefined(endpoint)) throw new ParentResourceError();

      dispatch(resourceTypesActions.parentRequest());
      const response = await axios.get(endpoint);
      if (_.isEmpty(response.data.data)) throw new ParentResourceError();
      dispatch(resourceTypesActions.parentSuccess(response.data));
      return Promise.resolve(response.data);
    } catch (error) {
      logErrorToConsole(error);
      const { custom } = error;
      let intlKey;

      if (custom) {
        ({ intlKey } = error);
      } else {
        const errorMessage = generateHttpErrorMessage(error);
        ({ intlKey } = errorMessage);
      }

      dispatch(resourceTypesActions.parentFailure(intlKey));

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            parentResourceType,
          }}
        />,
      );

      return Promise.reject(error);
    }
  };
}

export function getSelectedGroupsResourceAction() {
  return async (dispatch, getState) => {
    const resourceEndpoint = _.get(getState(), 'resourceTypes.selected.attributes.resourceEndpoint');
    try {
      const endpoint = endpoints.endpointForResource(resourceEndpoint, { creationGroups: true });
      dispatch(resourceTypesActions.groupsRequest());

      const response = await axios.get(endpoint);

      dispatch(resourceTypesActions.groupsSuccess(response.data));
      return Promise.resolve(response);
    } catch (error) {
      logErrorToConsole(error);
      const { custom } = error;
      let intlKey;

      if (custom) {
        ({ intlKey } = error);
      } else {
        const errorMessage = generateHttpErrorMessage(error);
        ({ intlKey } = errorMessage);
      }

      dispatch(resourceTypesActions.groupsFailure(intlKey));

      return Promise.reject(error);
    }
  };
}

/*
 * byType: Array[]
 * byPermission: string
 * primary: boolean
 */
export function filterResourceTypesAction(filter = {}, options = {}) {
  return async (dispatch, getState) => {
    try {
      const primary = _.get(filter, 'primary', true);
      const byType = _.get(filter, 'byType');
      const byPermission = _.get(filter, 'byPermission');
      const getParent = _.get(options, 'getParent');
      const getGroups = _.get(options, 'getGroups');
      const selected = primary
        ? _.get(getState(), 'resourceTypes.selected')
        : _.get(getState(), 'resourceTypes.secondarySelected');
      let data = _.get(getState(), 'resourceTypes.data');
      if (byType) {
        data =
          data
            .filter(resourceType => byType
              .includes(resourceType.attributes.resourceType));
      }

      if (byPermission) {
        data =
          data
            .filter(resourceType => resourceType
              .attributes
              .permissions
              .includes(byPermission));
      }

      const response = { ...filter, data };
      dispatch(resourceTypesActions.filter(response));
      if (selected.id) {
        const isInFilteredData = data.find(datum => datum.id === selected.id);
        let hasParent = _.get(selected, 'attributes.parentResourceType');

        if (!isInFilteredData) {
          hasParent = _.get(data[0], 'attributes.parentResourceType');
          primary
            ? await dispatch(setSelectedResourceTypeAction(data[0]))
            : await dispatch(setSecondarySelectedResourceTypeAction(data[0]));
        }

        if (getParent && hasParent) {
          await dispatch(getSelectedParentResourceAction());
        }

        if (getGroups) {
          await dispatch(getSelectedGroupsResourceAction());
        }
      }

      return Promise.resolve(response);
    } catch (error) {
      logErrorToConsole(error);
      return Promise.reject(error);
    }
  };
}

export function generateResourceTypeCSVAction(groupId) {
  return async (dispatch, getState) => {
    const resourceEndpoint = _.get(getState(), 'resourceTypes.selected.attributes.resourceEndpoint');
    try {
      const endpoint = endpoints.endpointForResource(resourceEndpoint, { groupId, report: true });
      const response = await axios
        .get(endpoint)
        .then((res) => {
          const { data, headers } = res;
          const filename = headers['content-disposition']
            .split(';')
            .find(n => n.includes('filename='))
            .split('=')
            .pop()
            .replace(/(^"|"$)/g, '');
          const url = window.URL.createObjectURL(new Blob([data], { type: 'text/csv' }));
          const link = document.createElement('a');
          link.href = url;
          link.download = filename || 'report.csv';
          link.click();
          window.URL.revokeObjectURL(url);
          return link;
        });
      return Promise.resolve(response);
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { statusCode } = errorMessage;
      let { intlKey } = errorMessage;

      if (statusCode === 400) {
        intlKey = 'report.error.query-too-broad';
      }

      dispatch(resourceTypesActions.reportFailure(intlKey));

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );

      return Promise.reject(error);
    }
  };
}
